@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

body {
  min-height: 100vh;
  background: linear-gradient(135deg, #b798f5, #e1d4ff, #ffffff, #aee3f0);
  background-size: 300% 300%;
  animation: gradientShift 10s ease infinite;
  color: #222;
  display: flex;
  justify-content: center;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.portfolio {
  max-width: 1100px;
  width: 100%;
  padding: 2rem;
  text-align: center;
  position: relative;
  z-index: 10;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}

.logo {
  font-weight: 700;
  font-size: 1.6rem;
  color: #7f56d9;
}

.glass-menu {
  display: flex;
  gap: 1.5rem;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(14px);
  border-radius: 30px;
  padding: 0.6rem 1.2rem;
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  border: 1px solid rgba(255,255,255,0.4);
  transition: background 0.3s ease, transform 0.3s ease;
}

.glass-menu:hover {
  background: rgba(255, 255, 255, 0.35);
  transform: translateY(-2px);
}

.glass-menu > div {
  color: #333; 
  text-decoration: none;
  font-weight: 500;

  padding: 0.6rem 1rem; 
  border-radius: 25px; 
  transition: all 0.3s ease; 
  cursor: pointer;
  position: relative; 
  overflow: hidden; 
  z-index: 1; 
}

.glass-menu > div:hover {
  color: #4a3aff; 
  background: rgba(255, 255, 255, 0.5); 
  transform: translateY(-2px); 

  box-shadow: 0 0 10px rgba(138, 107, 245, 0.5), 
              0 0 20px rgba(255, 255, 255, 0.4); 
}

.glass-menu > div::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%; 
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent); 
  transform: skewX(-20deg); 
  transition: all 0.7s ease; 
}

.glass-menu > div:hover::before {
  left: 100%; 
}

.hero {
  margin-bottom: 3rem;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(12px);
  border-radius: 20px;
  padding: 3rem;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  position: relative;
}

.avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid rgba(255,255,255,0.7);
  margin-bottom: 1rem;
}

.hero h1 {
  font-size: 2.3rem;
  font-weight: 700;
  color: #6b3efc;
}

.hero h1 span {
  color: #8e6efc;
}

.hero p {
  margin-top: 1rem;
  font-size: 1rem;
  color: #555;
  line-height: 1.6;
}

.skills {
  margin-bottom: 3rem;
}

.skills h2 {
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.skill-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  justify-content: center;
}

.skill {
  background: rgba(255,255,255,0.25);
  border: 1px solid rgba(255,255,255,0.3);
  backdrop-filter: blur(10px);
  border-radius: 25px;
  padding: 0.5rem 1rem;
  font-weight: 500;
  color: #333;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
  
  cursor: pointer; 
  transition: all 0.3s ease; 
  position: relative; 
  overflow: hidden; 
}

.skill:hover {
  color: #4a3aff; 
  background: rgba(255, 255, 255, 0.5); 
  transform: translateY(-3px) scale(1.03); 
  box-shadow: 0 0 10px rgba(138, 107, 245, 0.5), 
              0 0 20px rgba(255, 255, 255, 0.4), 
              0 8px 15px rgba(0,0,0,0.1); 
}


.skill::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%; 
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transform: skewX(-20deg); 
  transition: all 0.7s ease;
  z-index: 2;
}

.skill:hover::before {
  left: 100%; 
}
.projects {
  margin-bottom: 3rem;
}

.projects h2 {
  margin-bottom: 2rem;
}

.project-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.card {
  background: rgba(255,255,255,0.25);
  border-radius: 20px;
  backdrop-filter: blur(15px);
  box-shadow: 0 8px 25px rgba(0,0,0,0.1);
  padding: 1rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.15);
}

.card img {
  width: 100%;
  border-radius: 15px;
  margin-bottom: 1rem;
}

.card h3 {
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #333;
}

.card p {
  font-size: 0.95rem;
  color: #555;
}

.contact {
  background: rgba(255,255,255,0.25);
  backdrop-filter: blur(12px);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.08);
  margin-bottom: 3rem;
}

.contact h2 {
  margin-bottom: 1rem;
  color: #4a3aff;
}

.contact button {
  margin-top: 1.5rem;
  background: linear-gradient(135deg, #8e6efc, #6b3efc, #6d31ee, #4d1ef8, #7936fd, #8e6efc, #8f71f1, #a480f3);
  color: rgb(236, 208, 245);
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 30px;
  cursor: pointer;
  font-weight: 600;
  transition: 0.3s;
}

.contact button:hover {
  opacity: 0.85;
}

footer {
  font-size: 0.85rem;
  color: #777;
}
